import React, { Fragment } from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { Link } from "gatsby"
import { presentationInfoList } from "../contents/presentation-info";

//<Link to="/publications/">more</Link>

const Presentations = () => (
    <Layout>
        <SEO title="Presentations" keywords={[`Pei-Yao Hung`, `service`, `Human-Computer Interaction`, `user experience`, `researcher`, `publications`, `CSCW`, `PervasiveHealth`, `CHI`, `MobileHCI`, `ECSCW`, `IDC`, `C&T`]} />
        <div className="content">
            <div className="title is-3 is-spaced  is-size-4-mobile">Presentations</div>

            <div className="columns">
                <div className="column"><figure className="image">
                    <picture>
                        <source type="image/webp" srcset={"/images/presentation/presentation_pervasive_health_2019.webp"}/>
                        <source type="image/jpeg" srcset={"/images/presentation/presentation_pervasive_health_2019.jpg"}/>
                        <img className="image" src={"/images/presentation/presentation_pervasive_health_2019.jpg"} alt=""/>
                    </picture>
                    <figcaption style={{ textAlign: "center" }}>
                        I presented our paper at PervasiveHealth 2019 conference.
                </figcaption>
                </figure>
                </div>
            </div>

            <div className="content has-text-justified">
                <div className="subtitle is-4 is-spaced is-size-5-mobile">Posters</div>
                {
                    presentationInfoList.filter((pub) => { return pub.type == "poster"; }).sort((pubA, pubB) => { return new Date(pubB.date) - new Date(pubA.date); }).map((episode, index) => {
                        return <div key={index}>
                            <div className="columns">
                                <div className="column is-four-fifths">
                                    <div className="is-size-5 is-size-6-mobile has-text-weight-semibold">{episode.title}</div>
                                    <div>{episode.occasion}</div>
                                </div>
                                <div className="column is-clearfx">
                                    <div className="is-pulled-left">{episode.date_label}</div>
                                    <div className="is-pulled-right">{episode.location}</div>
                                </div>
                            </div>
                            <br />
                        </div>
                    })
                }
                <hr />
                <div className="subtitle is-4 is-spaced is-size-5-mobile">Demonstrations</div>
                {
                    presentationInfoList.filter((pub) => { return pub.type == "demo"; }).sort((pubA, pubB) => { return new Date(pubB.date) - new Date(pubA.date); }).map((episode, index) => {
                        return <div key={index}>
                            <div className="columns">
                                <div className="column is-four-fifths">
                                    <div className="is-size-5 is-size-6-mobile has-text-weight-semibold">{episode.title}</div>
                                    <div>{episode.occasion}</div>
                                </div>
                                <div className="column is-clearfx">
                                    <div className="is-pulled-left">{episode.date_label}</div>
                                    <div className="is-pulled-right">{episode.location}</div>
                                </div>
                            </div>
                            <br />
                        </div>
                    })
                }
                <hr />
                <div className="subtitle is-4 is-spaced is-size-5-mobile">Talks</div>
                {
                    presentationInfoList.filter((pub) => { return pub.type == "talk"; }).sort((pubA, pubB) => { return new Date(pubB.date) - new Date(pubA.date); }).map((episode, index) => {
                        return <div key={index}>
                            <div className="columns">
                                <div className="column is-four-fifths">
                                    <div className="is-size-5 is-size-6-mobile has-text-weight-semibold">{episode.title}</div>
                                    <div>{episode.occasion}</div>
                                </div>
                                <div className="column is-clearfx">
                                    <div className="is-pulled-left">{episode.date_label}</div>
                                    <div className="is-pulled-right">{episode.location}</div>
                                </div>
                            </div>
                            <br />
                        </div>
                    })
                }
                <hr />
            </div>

        </div>
    </Layout>
)

export default Presentations

